@import url(https://fonts.googleapis.com/css?family=Open+Sans|Pacifico);
body
{
  margin:0;
  padding:0;
  font-family:sans-serif
}
body,
p
{
  margin:0
}
p
{
  display:inline-block
}
.App
{
  background-color:#1ee494;
  height:100vh;
  width:100vw;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
  align-items:center;
  -ms-flex-pack:center;
  justify-content:center
}
.chatroom
{
  width:360px;
  height:600px;
  background-color:rgba(0,0,0,.7);
  border-radius:5px;
  overflow:hidden;
  -webkit-box-shadow:0 0 8px 0 rgba(0,0,0,.3);
  box-shadow:0 0 8px 0 rgba(0,0,0,.3)
}
.chatroom h3
{
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  background-color: #fff;
  color:#0a4f38;
  margin:0;
  height:40px;
  font-size:20px;display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
  align-items:center;
  -ms-flex-pack:center;
  justify-content:center}
  .chatroom .chats
  {
    -webkit-box-sizing:border-box;
    box-sizing:border-box;
    padding:0 20px;
    margin:10px 0 0;
    height:510px;
    overflow-y:scroll
  }
  .chatroom .chats .chat
  {
    background:hsla(0,0%,100%,.8);
    position:relative;
    padding:5px 13px;
    font-size:14px;
    border-radius:10px;
    list-style:none;
    float:left;
    clear:both;
    margin:10px 0;
    /* max-width:200px */
  }
  .chatroom .chats .chat img
  {
    width:20px;
    height:20px;
    border-radius:50%;
    position:absolute;
    top:-10px;
    left:0
  }
  .chatroom .chats .chat.right
  {
    float:right;clear:both
  }
  .chatroom .chats .chat a
  {
    text-decoration:none;color:#3498db
  }
  .chatroom .input
  {height:40px;
    width:100%;
    background: #0a4f38;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:row;
    flex-direction:row;
    -ms-flex-align:center;
    align-items:center
  }.chatroom .input input[type=text]
  {
    background:hsla(0,0%,100%,.5);
    outline:0;
    border:2px solid #0a4f38;
    padding:0 5px;
    border-top-left-radius:5px;
    border-bottom-left-radius:5px;
    margin-left:10px;width:270px
  }
  .chatroom .input input[type=submit],.chatroom .input input[type=text]
  {
    height:30px;-webkit-box-sizing:border-box;
    box-sizing:border-box;
    font-size:14px
  }
  .chatroom .input input[type=submit]
  {
    background:transparent;
    border:1px solid #1ee494;
    width:70px;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
    cursor:pointer;
    -webkit-transition:all .3s ease;
    -o-transition:all .3s ease;
    transition:all .3s ease
  }
  .chatroom .input input[type=submit]:hover
  {
    background:#1ee494;
    color:#fff
  }


